

  /* =Type
  ----------------------------------------------- */
  h1, h2, h3, h4, h5, h6, p, ol, ul, dl, figure {
    margin-top   : calc(var(--line-height) / 2);
    margin-bottom: calc(var(--line-height) / 2);
    max-width: 33em;
  }
  h1, h2, h3, h4, h5, h6 {
    margin-top: var(--line-height);
    font-weight: inherit;
    font-family: "Roboto Slab", "Source Sans Pro", "Helvetica Neue", Helvetica, sans-serif;
  }
  h1, h2 {
    text-transform: uppercase;
    font-weight: 300;
  }
  h1 strong, h2 strong {
    font-weight: 400;
  }
  h1, h2, h3, h4, h5, h6, p {
    max-width: 40em;
  }

  h1 {
    font-size: 2.5em;
    margin-top   : calc(var(--line-height) / 2.5em);
    margin-bottom: calc(var(--line-height) / 2.5em / 2);
  }

  h2 {
    font-size: 2em;
    margin-top   : calc(var(--line-height) / 2em);
    margin-bottom: calc(var(--line-height) / 2em / 2);
  }

  h3 {
    font-size: 1.5em;
    margin-top   : calc(var(--line-height) / 1.5em);
    margin-bottom: calc(var(--line-height) / 1.5em / 2);
  }

  h4 {
    font-size: 1.25em;
    margin-top   : calc(var(--line-height) / 1.25em);
    margin-bottom: calc(var(--line-height) / 1.25em / 2);
  }

  h5, h6 {
    font-size: 1em;
    font-style: italic;
  }

  h6 {
    font-weight: normal;
  }
